.timer--container {
  border: 3px solid rgb(108, 67, 37);
  border-radius: 20px;

  padding: 0.2em;

  width: clamp(50px, 150px, 200px);

  display: flex;
  align-items: center;
  justify-content: space-around;
}

.timer--toggle--button {
  background-color: transparent;
  border: 0;
  padding: 0;
}

.timer--display {
  font-weight: 500;
  font-size: clamp(1em, 1.3em, 2em);
}
