nav {
  height: 2vh;
  /* width: 100vw; */

  background-color: #fbf9f4;

  display: flex;
  justify-content: space-between;
  align-items: center;

  padding: 2em 0.5em;
}

.nav--title {
  color: #26c065;
  font-weight: 600;
  font-size: clamp(1.25em, 1.5vw, 1.5vw);
  letter-spacing: -0.03em;

  /* width: clamp(5vw, 9vw, 12vw); */

  white-space: nowrap;
}

.nav--select {
  margin-right: 0.5em;

  background-color: transparent;

  border: none;
  padding-right: 0.75em;
  font-size: inherit;
  cursor: inherit;

  background-color: transparent;

  color: #26c065;
}
